"use client";

import { Check, Calendar, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import { format } from "date-fns";
import { ptBR } from "date-fns/locale";

type AppointmentListProps = {
  scheduleData: {
    [date: string]: {
      [time: string]: {
        classSelected: string;
        studentName: string;
        completed?: boolean;
      };
    };
  };
  onComplete: (date: string, time: string) => void;
  onReschedule: (date: string, time: string) => void;
};

export function AppointmentList({ scheduleData, onComplete, onReschedule }: AppointmentListProps) {
  const appointments = Object.entries(scheduleData).flatMap(([date, times]) =>
    Object.entries(times).map(([time, data]) => ({
      date,
      time,
      ...data,
    }))
  ).sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());

  if (appointments.length === 0) {
    return (
      <div className="text-center p-6 bg-white rounded-lg border-2 border-red-100">
        <p className="text-gray-500">Nenhum agendamento realizado</p>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg border-2 border-red-100 overflow-hidden">
      <div className="p-4 bg-red-50 border-b-2 border-red-100">
        <h2 className="text-lg font-semibold text-red-800">Agendamentos Realizados</h2>
      </div>
      <div className="divide-y divide-red-100">
        {appointments.map(({ date, time, classSelected, studentName, completed }) => (
          <div
            key={`${date}-${time}`}
            className="p-4 hover:bg-red-50/50 transition-colors"
          >
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <p className="font-medium text-red-900">
                  {format(new Date(date), "EEEE, dd 'de' MMMM", { locale: ptBR })}
                </p>
                <p className="text-sm text-gray-600">{time}</p>
                <p className="mt-1">
                  <span className="font-medium">Turma:</span> {classSelected}
                </p>
                <p>
                  <span className="font-medium">Tutorado:</span> {studentName}
                </p>
              </div>
              <div className="flex flex-col gap-2">
                <Button
                  variant="outline"
                  size="sm"
                  className={`${
                    completed
                      ? "bg-green-50 text-green-700 border-green-200 hover:bg-green-100 hover:text-green-800"
                      : "hover:bg-red-50 hover:text-red-800"
                  }`}
                  onClick={() => onComplete(date, time)}
                >
                  {completed ? (
                    <Check className="h-4 w-4" />
                  ) : (
                    <X className="h-4 w-4" />
                  )}
                </Button>
                <Button
                  variant="outline"
                  size="sm"
                  className="hover:bg-red-50 hover:text-red-800"
                  onClick={() => onReschedule(date, time)}
                >
                  <Calendar className="h-4 w-4" />
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
