"use client";

import { useState, FormEvent } from "react";
import { Calendar } from "@/components/ui/calendar";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { format, isWeekend } from "date-fns";
import { ptBR } from "date-fns/locale";

type ReagendamentoFormProps = {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  currentAppointment: {
    date: string;
    time: string;
    classSelected: string;
    studentName: string;
  };
  onReschedule: (oldDate: string, oldTime: string, newDate: string, newTime: string, classSelected: string, studentName: string) => void;
  turno: "turno1" | "turno2";
};

const TURNO_1_SLOTS = [
  { time: "14:30 - 15:20", label: "AULA 01" },
  { time: "15:20 - 16:10", label: "AULA 02" },
  { time: "16:10 - 16:30", label: "CAFÉ" },
  { time: "16:30 - 17:20", label: "AULA 03" },
  { time: "17:20 - 18:10", label: "AULA 04" },
  { time: "18:10 - 19:00", label: "AULA 05" },
  { time: "19:00 - 19:50", label: "JANTAR" },
  { time: "19:50 - 20:40", label: "AULA 06" },
  { time: "20:40 - 21:30", label: "AULA 07" },
];

const TURNO_2_SLOTS = [
  { time: "7:00 - 7:50", label: "AULA 1" },
  { time: "7:50 - 8:40", label: "AULA 2" },
  { time: "8:40 - 9:00", label: "CAFÉ" },
  { time: "9:00 - 9:50", label: "AULA 3" },
  { time: "9:50 - 10:40", label: "AULA 4" },
  { time: "10:40 - 11:30", label: "AULA 5" },
  { time: "11:30 - 12:20", label: "AULA 6" },
  { time: "12:30 - 13:10", label: "AULA 7" },
  { time: "13:10 - 14:00", label: "AULA 8" },
];

const TURNO_1_CLASSES = ["1ªA", "1ªB", "1ªC", "2ªA", "2ªB", "2ªC", "3ªA", "3ªB", "3ªC"];
const TURNO_2_CLASSES = ["6ºA", "6ºB", "6ºC", "7ºA", "7ºB", "7ºC", "8ºA", "8ºB", "8ºC", "8ºD", "9ºA", "9ºB", "9ºC"];

export function ReagendamentoForm({
  open,
  onOpenChange,
  currentAppointment,
  onReschedule,
  turno,
}: ReagendamentoFormProps) {
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(
    new Date(currentAppointment.date)
  );
  const [selectedTime, setSelectedTime] = useState(currentAppointment.time);
  const [selectedClass, setSelectedClass] = useState(currentAppointment.classSelected);
  const [studentName, setStudentName] = useState(currentAppointment.studentName);

  const slots = turno === "turno1" ? TURNO_1_SLOTS : TURNO_2_SLOTS;
  const classes = turno === "turno1" ? TURNO_1_CLASSES : TURNO_2_CLASSES;

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    if (selectedDate && selectedTime && selectedClass && studentName) {
      onReschedule(
        currentAppointment.date,
        currentAppointment.time,
        format(selectedDate, "yyyy-MM-dd"),
        selectedTime,
        selectedClass,
        studentName
      );
      onOpenChange(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle className="text-xl font-bold text-red-800">
            Reagendar Atendimento
          </DialogTitle>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Nova Data</label>
            <Calendar
              mode="single"
              selected={selectedDate}
              onSelect={setSelectedDate}
              className="border-2 border-red-200 rounded-lg p-4"
              modifiers={{
                disabled: (date) => isWeekend(date),
              }}
              modifiersStyles={{
                disabled: { color: "#9CA3AF", textDecoration: "line-through" },
              }}
              locale={ptBR}
            />
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Novo Horário</label>
            <Select value={selectedTime} onValueChange={setSelectedTime}>
              <SelectTrigger className="w-full border-red-200 hover:border-red-300">
                <SelectValue placeholder="Selecione o horário" />
              </SelectTrigger>
              <SelectContent>
                {slots
                  .filter((slot) => !["CAFÉ", "JANTAR"].includes(slot.label))
                  .map((slot) => (
                    <SelectItem key={slot.time} value={slot.time}>
                      {slot.time} ({slot.label})
                    </SelectItem>
                  ))}
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Turma</label>
            <Select value={selectedClass} onValueChange={setSelectedClass}>
              <SelectTrigger className="w-full border-red-200 hover:border-red-300">
                <SelectValue placeholder="Selecione a turma" />
              </SelectTrigger>
              <SelectContent>
                {classes.map((cls) => (
                  <SelectItem key={cls} value={cls}>
                    {cls}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Nome do Tutorado</label>
            <Input
              value={studentName}
              onChange={(e) => setStudentName(e.target.value)}
              className="border-red-200 hover:border-red-300"
              placeholder="Digite o nome do tutorado"
            />
          </div>

          <div className="flex justify-end gap-2">
            <Button
              type="button"
              variant="outline"
              onClick={() => onOpenChange(false)}
              className="border-red-200 hover:bg-red-50 hover:text-red-800"
            >
              Cancelar
            </Button>
            <Button
              type="submit"
              className="bg-red-700 hover:bg-red-800 text-white"
            >
              Confirmar Reagendamento
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
