"use client";

import React, { useState, useEffect } from "react";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Select, SelectTrigger, SelectValue, SelectContent, SelectItem } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Calendar } from "@/components/ui/calendar";
import { format, isWeekend, startOfMonth, endOfMonth, eachDayOfInterval } from "date-fns";
import { ptBR } from "date-fns/locale";
import { UserCircle2, Users } from "lucide-react";
import { Sheet, SheetContent, SheetHeader, SheetTitle } from "@/components/ui/sheet";
import { AppointmentList } from "@/components/AppointmentList";
import { ReagendamentoForm } from "@/components/ReagendamentoForm";
// @ts-ignore
import html2canvas from 'html2canvas';
// @ts-ignore
import jsPDF from 'jspdf';

const USERS = [
  "ADRIANA", "ANA PAULA", "ANDREIA", "BRUNA", "CLAUDIA", "ELENITA", "ELIESER", "GIANI", "GISÉLIA", "GRAICE",
  "JOICE", "KATIA", "KELVYN", "LUCIMAR", "LUIZ GAMA", "MARILEIDE", "MARCOS (MANHÃ)", "MARCOS (TARDE)", "MICHELLE",
  "MOISÉS", "PATRÍCIA", "RAFAEL", "RAUL", "RITA", "ROBERT", "ROBERTO", "ROSANE", "ROSIANI", "SEBÁ", "SÉRGIO",
  "SILVANA (MANHÃ)", "SILVANA (TARDE)", "THAENA", "THAMIRIS", "THAIS", "THIAGO", "VANESSA"
].sort();

const TURNO_1_SLOTS = [
  { time: "14:30 - 15:20", label: "AULA 01" },
  { time: "15:20 - 16:10", label: "AULA 02" },
  { time: "16:10 - 16:30", label: "CAFÉ" },
  { time: "16:30 - 17:20", label: "AULA 03" },
  { time: "17:20 - 18:10", label: "AULA 04" },
  { time: "18:10 - 19:00", label: "AULA 05" },
  { time: "19:00 - 19:50", label: "JANTAR" },
  { time: "19:50 - 20:40", label: "AULA 06" },
  { time: "20:40 - 21:30", label: "AULA 07" },
];

const TURNO_1_CLASSES = ["1ªA", "1ªB", "1ªC", "2ªA", "2ªB", "2ªC", "3ªA", "3ªB", "3ªC"];

const TURNO_2_SLOTS = [
  { time: "7:00 - 7:50", label: "AULA 1" },
  { time: "7:50 - 8:40", label: "AULA 2" },
  { time: "8:40 - 9:00", label: "CAFÉ" },
  { time: "9:00 - 9:50", label: "AULA 3" },
  { time: "9:50 - 10:40", label: "AULA 4" },
  { time: "10:40 - 11:30", label: "AULA 5" },
  { time: "11:30 - 12:20", label: "AULA 6" },
  { time: "12:30 - 13:10", label: "AULA 7" },
  { time: "13:10 - 14:00", label: "AULA 8" },
];

const TURNO_2_CLASSES = [
  "6ºA", "6ºB", "6ºC", "7ºA", "7ºB", "7ºC", "8ºA", "8ºB", "8ºC", "8ºD", "9ºA", "9ºB", "9ºC"
];

function getMonthDays(year: number, month: number) {
  const start = startOfMonth(new Date(year, month));
  const end = endOfMonth(new Date(year, month));
  return eachDayOfInterval({ start, end });
}

function isWeekendDay(date: Date) {
  return isWeekend(date);
}

type ScheduleEntry = {
  classSelected: string;
  studentName: string;
  completed?: boolean;
};

type ScheduleData = {
  [date: string]: {
    [time: string]: ScheduleEntry;
  };
};

export default function Home() {
  const [currentUser, setCurrentUser] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<"turno1" | "turno2">("turno1");
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  const [scheduleData, setScheduleData] = useState<ScheduleData>({});
  const [showUserDialog, setShowUserDialog] = useState(true);
  const [showDaySheet, setShowDaySheet] = useState(false);
  const [showUserListDialog, setShowUserListDialog] = useState(false);
  const [showRescheduleForm, setShowRescheduleForm] = useState(false);
  const [selectedAppointment, setSelectedAppointment] = useState<{
    date: string;
    time: string;
    classSelected: string;
    studentName: string;
  } | null>(null);

  // Load saved data from localStorage on user change
  useEffect(() => {
    if (currentUser) {
      const saved = localStorage.getItem("scheduleData");
      if (saved) {
        setScheduleData(JSON.parse(saved));
      } else {
        setScheduleData({});
      }
    }
  }, [currentUser]);

  // Save data to localStorage on scheduleData change
  useEffect(() => {
    if (currentUser) {
      localStorage.setItem("scheduleData", JSON.stringify(scheduleData));
    }
  }, [scheduleData, currentUser]);

  const hasScheduleForDate = (date: Date) => {
    const dateStr = format(date, "yyyy-MM-dd");
    return scheduleData[dateStr] && Object.keys(scheduleData[dateStr]).length > 0;
  };

  const handleClassChange = (dateStr: string, time: string, classSelected: string) => {
    setScheduleData((prev) => {
      const newData = { ...prev };
      if (!newData[dateStr]) newData[dateStr] = {};
      if (!newData[dateStr][time]) newData[dateStr][time] = { classSelected: "", studentName: "" };
      newData[dateStr][time].classSelected = classSelected;
      return newData;
    });
  };

  const handleStudentNameChange = (dateStr: string, time: string, studentName: string) => {
    setScheduleData((prev) => {
      const newData = { ...prev };
      if (!newData[dateStr]) newData[dateStr] = {};
      if (!newData[dateStr][time]) newData[dateStr][time] = { classSelected: "", studentName: "" };
      newData[dateStr][time].studentName = studentName;
      return newData;
    });
  };

  const exportToImage = async (format: "png" | "jpg" | "pdf") => {
    const element = document.getElementById("calendar-container");
    if (!element) return;
    if (format === "pdf") {
      const canvas = await html2canvas(element);
      const imgData = canvas.toDataURL("image/png");
      const pdf = new jsPDF();
      const imgProps = pdf.getImageProperties(imgData);
      const pdfWidth = pdf.internal.pageSize.getWidth();
      const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
      pdf.addImage(imgData, "PNG", 0, 0, pdfWidth, pdfHeight);
      pdf.save("schedule.pdf");
    } else {
      const canvas = await html2canvas(element);
      const imgData = canvas.toDataURL(`image/${format}`);
      const link = document.createElement("a");
      link.href = imgData;
      link.download = `schedule.${format}`;
      link.click();
    }
  };

  const renderTimeSlots = (date: Date, slots: typeof TURNO_1_SLOTS, classes: string[]) => {
    const dateStr = format(date, "yyyy-MM-dd");
    return slots.map(({ time, label }) => {
      const isCafeOrJantar = label === "CAFÉ" || label === "JANTAR";
      const entry = scheduleData[dateStr]?.[time] || { classSelected: "", studentName: "" };
      return (
        <div key={time} className="flex items-center space-x-4 border-b border-red-100 py-3 px-2 hover:bg-red-50/50 transition-colors">
          <div className="w-36 font-medium text-red-900">{time} ({label})</div>
          {isCafeOrJantar ? (
            <div className="italic text-red-700 font-semibold">{label}</div>
          ) : (
            <>
              <div className="w-24">
                <Select
                  value={entry.classSelected}
                  onValueChange={(value) => handleClassChange(dateStr, time, value)}
                >
                  <SelectTrigger className="w-full border-red-200 hover:border-red-300 focus:ring-red-200">
                    <SelectValue placeholder="Turma" />
                  </SelectTrigger>
                  <SelectContent className="border-red-200">
                    {classes.map((cls) => (
                      <SelectItem key={cls} value={cls}>
                        {cls}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <Input
                type="text"
                placeholder="Nome do tutorado"
                value={entry.studentName}
                onChange={(e) => handleStudentNameChange(dateStr, time, e.target.value)}
                className="flex-1 border-red-200 hover:border-red-300 focus:ring-red-200 focus-visible:ring-red-200"
              />
            </>
          )}
        </div>
      );
    });
  };

  if (!currentUser) {
    return (
      <Dialog open={showUserDialog} onOpenChange={setShowUserDialog}>
        <DialogContent className="max-w-md">
          <DialogTitle>Selecione o usuário</DialogTitle>
          <DialogDescription>Escolha seu perfil para acessar o calendário</DialogDescription>
          <div className="w-full mt-4">
            <Select
              onValueChange={(value) => {
                setCurrentUser(value);
                setShowUserDialog(false);
              }}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Selecione o usuário" />
              </SelectTrigger>
              <SelectContent>
                {USERS.map((user) => (
                  <SelectItem key={user} value={user}>
                    {user}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </DialogContent>
      </Dialog>
    );
  }

  return (
    <main className="min-h-screen bg-white text-gray-900 p-4 flex flex-col">
      <h1 className="text-3xl font-bold mb-6 text-red-800 text-center">
        Agendamento de atendimentos das tutorias
      </h1>
      <div className="mb-6 text-center text-sm text-red-600 font-medium flex justify-between items-center">
        <div>
          Usuário atual: <span className="font-semibold">{currentUser}</span>
        </div>
        <div className="space-x-2">
          <Button
            variant="outline"
            size="sm"
            onClick={() => {
              setCurrentUser(null);
              setShowUserDialog(true);
            }}
            className="border-red-700 text-red-700 hover:bg-red-700 hover:text-white"
          >
            <UserCircle2 className="mr-1 h-4 w-4" />
            Mudar Perfil
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => setShowUserListDialog(true)}
            className="border-red-700 text-red-700 hover:bg-red-700 hover:text-white"
          >
            <Users className="mr-1 h-4 w-4" />
            Visualizar Calendário de Outros Usuários
          </Button>
        </div>
      </div>
      <div className="flex flex-col md:flex-row gap-6">
        <div className="flex-1">
          <Tabs
            value={activeTab}
            onValueChange={(value) => setActiveTab(value as "turno1" | "turno2")}
            className="flex-1"
          >
            <TabsList className="bg-red-100/50 p-1">
              <TabsTrigger value="turno1" className="data-[state=active]:bg-red-700 data-[state=active]:text-white">TURNO 1</TabsTrigger>
              <TabsTrigger value="turno2" className="data-[state=active]:bg-red-700 data-[state=active]:text-white">TURNO 2</TabsTrigger>
            </TabsList>
            <TabsContent value="turno1" className="mt-4">
              <div id="calendar-container" className="space-y-6 bg-white p-6 rounded-lg shadow-sm">
                <Calendar
                  mode="single"
                  selected={selectedDate}
                  onSelect={(date) => {
                    if (date && !isWeekend(date)) {
                      setSelectedDate(date);
                      setShowDaySheet(true);
                    }
                  }}
                  className="border-2 border-red-200 rounded-lg p-4 bg-white"
                  modifiers={{
                    disabled: (date) => isWeekend(date),
                    hasSchedule: (date) => hasScheduleForDate(date),
                  }}
                  modifiersStyles={{
                    disabled: { color: "#9CA3AF", textDecoration: "line-through" },
                    hasSchedule: { 
                      backgroundColor: "#FEE2E2", 
                      color: "#991B1B",
                      fontWeight: "bold",
                      border: "2px solid #991B1B"
                    }
                  }}
                  classNames={{
                    day: "h-10 w-10 text-center rounded-full hover:bg-red-50 focus:bg-red-100 focus:outline-none focus-visible:ring focus-visible:ring-red-300",
                    day_selected: "bg-red-700 text-white hover:bg-red-800",
                    day_today: "bg-red-100 font-bold text-red-900",
                    head_cell: "text-red-900 font-semibold",
                    caption: "mb-4 text-lg font-bold text-red-900"
                  }}
                />
              </div>
            </TabsContent>
            <TabsContent value="turno2" className="mt-4">
              <div id="calendar-container" className="space-y-6 bg-white p-6 rounded-lg shadow-sm">
                <Calendar
                  mode="single"
                  selected={selectedDate}
                  onSelect={(date) => {
                    if (date && !isWeekend(date)) {
                      setSelectedDate(date);
                      setShowDaySheet(true);
                    }
                  }}
                  className="border-2 border-red-200 rounded-lg p-4 bg-white"
                  modifiers={{
                    disabled: (date) => isWeekend(date),
                    hasSchedule: (date) => hasScheduleForDate(date),
                  }}
                  modifiersStyles={{
                    disabled: { color: "#9CA3AF", textDecoration: "line-through" },
                    hasSchedule: { 
                      backgroundColor: "#FEE2E2", 
                      color: "#991B1B",
                      fontWeight: "bold",
                      border: "2px solid #991B1B"
                    }
                  }}
                  classNames={{
                    day: "h-10 w-10 text-center rounded-full hover:bg-red-50 focus:bg-red-100 focus:outline-none focus-visible:ring focus-visible:ring-red-300",
                    day_selected: "bg-red-700 text-white hover:bg-red-800",
                    day_today: "bg-red-100 font-bold text-red-900",
                    head_cell: "text-red-900 font-semibold",
                    caption: "mb-4 text-lg font-bold text-red-900"
                  }}
                />
              </div>
            </TabsContent>
          </Tabs>
        </div>
        <div className="w-full md:w-96">
          <AppointmentList
            scheduleData={scheduleData}
            onComplete={(date, time) => {
              setScheduleData((prev) => {
                const newData = { ...prev };
                if (newData[date] && newData[date][time]) {
                  newData[date][time].completed = !newData[date][time].completed;
                }
                return newData;
              });
            }}
            onReschedule={(date, time) => {
              if (scheduleData[date] && scheduleData[date][time]) {
                setSelectedAppointment({
                  date,
                  time,
                  classSelected: scheduleData[date][time].classSelected,
                  studentName: scheduleData[date][time].studentName,
                });
                setShowRescheduleForm(true);
              }
            }}
          />
        </div>
      </div>

      <Sheet open={showDaySheet} onOpenChange={setShowDaySheet}>
        <SheetContent className="w-full sm:max-w-xl border-l-2 border-red-200">
          <SheetHeader className="border-b-2 border-red-100 pb-4">
            <SheetTitle className="text-2xl font-bold text-red-800">
              {selectedDate ? format(selectedDate, "EEEE, dd 'de' MMMM", { locale: ptBR }) : ""}
            </SheetTitle>
          </SheetHeader>
          <div className="mt-6 overflow-y-auto max-h-[calc(100vh-200px)]">
            <div className="space-y-6">
              {selectedDate && (
                activeTab === "turno1" 
                  ? renderTimeSlots(selectedDate, TURNO_1_SLOTS, TURNO_1_CLASSES)
                  : renderTimeSlots(selectedDate, TURNO_2_SLOTS, TURNO_2_CLASSES)
              )}
            </div>
          </div>
        </SheetContent>
      </Sheet>

      {selectedAppointment && (
        <ReagendamentoForm
          open={showRescheduleForm}
          onOpenChange={setShowRescheduleForm}
          currentAppointment={selectedAppointment}
          turno={activeTab}
          onReschedule={(oldDate, oldTime, newDate, newTime, classSelected, studentName) => {
            setScheduleData((prev) => {
              const newData = { ...prev };
              // Remove old appointment
              if (newData[oldDate] && newData[oldDate][oldTime]) {
                delete newData[oldDate][oldTime];
                if (Object.keys(newData[oldDate]).length === 0) {
                  delete newData[oldDate];
                }
              }
              // Add new appointment
              if (!newData[newDate]) newData[newDate] = {};
              newData[newDate][newTime] = {
                classSelected,
                studentName,
                completed: false,
              };
              return newData;
            });
          }}
        />
      )}

      <div className="mt-8 flex justify-center space-x-4">
        <Button onClick={() => exportToImage("png")} className="bg-red-700 hover:bg-red-800 text-white px-6">
          Exportar PNG
        </Button>
        <Button onClick={() => exportToImage("jpg")} className="bg-red-700 hover:bg-red-800 text-white px-6">
          Exportar JPG
        </Button>
        <Button onClick={() => exportToImage("pdf")} className="bg-red-700 hover:bg-red-800 text-white px-6">
          Exportar PDF
        </Button>
      </div>
      <Dialog open={showUserListDialog} onOpenChange={setShowUserListDialog}>
        <DialogContent className="max-w-3xl">
          <DialogTitle className="text-xl font-bold text-red-800 mb-4">
            Calendários de Outros Usuários
          </DialogTitle>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {USERS.filter(user => user !== currentUser).map((user) => (
              <Button
                key={user}
                variant="outline"
                className="justify-start border-red-200 hover:bg-red-50 hover:text-red-800"
                onClick={() => {
                  const savedData = localStorage.getItem(`scheduleData_${user}`);
                  if (savedData) {
                    setScheduleData(JSON.parse(savedData));
                    setCurrentUser(user);
                    setShowUserListDialog(false);
                  }
                }}
              >
                <UserCircle2 className="mr-2 h-4 w-4" />
                {user}
              </Button>
            ))}
          </div>
        </DialogContent>
      </Dialog>

      <footer className="mt-12 pb-6 text-center text-sm text-red-700 font-medium">
        Escola Estadual PEI Professor Mario Manoel Dantas de Aquino, Sistema de Tutoria
      </footer>
    </main>
  );
}
